.logo{
    width: 50px;
}
@import url('https://fonts.googleapis.com/css2?family=Raleway&family=Ubuntu:wght@300&family=Work+Sans:wght@800&display=swap');
.carousel-main{
    margin: 40px auto;
    max-width: 80%;

}
.carousel-head{
    margin-top: 0px;
    color: rgb(173, 173, 173);
    font-size: 4rem;
    font-weight: 900;
    font-family: 'raleway',sans-serif;
}
#img{
max-width: 500px;
border-radius: 6px;
max-height: 300px;

}
#imggb{
    width: 100%;
}

.home-content{
    
    display: flex;
    flex-direction: row;
}
.home-img{
    width: 300px;
    border-radius: 7px;
}
.home-inner{
    margin: 10px 30px;

}

.home-foot{
    margin-bottom: -100px;
    margin-left: -100px;
   
    width: 600px;
    background-color: rgb(208, 150, 16);
    color: white;
    height: 200px;
    font-family: 'Work Sans', sans-serif;
}
.link{
    color: white;
}
#btn{
    border: 2px solid green;
    border-radius: 5px;
    padding: 10px 30px ;
    color: white;
    font-weight: 500;
    background-color: green;
}
#btn:hover{
    transition: all 0.3s ease-in-out;
    transform: translateY(-5px);
    border: 2px solid green;
    background-color: transparent;
    color: rgb(92, 86, 86);
}
@media only screen and (max-width: 720px){
 
     .home-foot{
         width: 100%;
     }
    #img{
        max-width: 500px;
        border-radius: 6px;
        max-height: 250px;
        
        }
    .home-content{
        display: flex;
        flex-direction: column;
    }
  
}