.volunteer-head{
    margin-top: 30px;
    width: 100%;
    height: 300px;
    padding: 100px;
    font-size: 3rem;
    color: white;
    font-weight: 800px;
    font-family: 'Work Sans',sans-serif;

    background-color: rgb(31, 130, 31);
}
.volunteer-content{
    display: flex;
    flex-direction: row;
    padding: 30px;
}
#img2{
    max-width: 400px;
    border-radius: 10px;
    height: max-content;
    padding: 10px;
}
.form{
    width: 100%;
    min-height: max-content;
}
@media only screen and (max-width: 720px){
 
    #img2{
        max-width: 100%;
    }
   .volunteer-content{
       display: flex;
       flex-direction: column;
   }
 
}