.contact-head{
    margin-top: 30px;
    width: 100%;
    height: 300px;
    padding: 100px;
    font-size: 3rem;
    color: white;
    font-weight: 800px;
    font-family: 'Work Sans',sans-serif;

    background-color: rgb(31, 130, 31);
}
.bold{
 
    font-weight: 700px;
}
.flaticon{
    width: 50px;
}
.flat{
    margin-top: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}
@media only screen and (max-width: 720px){
.flat{
    display: flex;
    flex-direction: column;
}
}